"use client";

import Link from 'next/link';
import { motion } from 'framer-motion';
import { CSSProperties } from 'react';
import { useState, useEffect } from 'react';
import Image from 'next/image';

export default function Hero() {
  // Initialize responsive state
  const [isLargeScreen, setIsLargeScreen] = useState(false);
  
  // Move window checks to useEffect
  useEffect(() => {
    const handleResize = () => {
      setIsLargeScreen(window.innerWidth >= 1024);
    };
    
    // Set initial value
    handleResize();
    
    // Add event listener
    window.addEventListener('resize', handleResize);
    
    // Clean up
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  // Animation variants
  const headingVariants = {
    hidden: {},
    visible: {
      transition: {
        staggerChildren: 0.05,
      }
    }
  };

  const letterVariants = {
    hidden: { 
      opacity: 0, 
      y: 20 
    },
    visible: { 
      opacity: 1, 
      y: 0,
      transition: {
        duration: 0.4,
      }
    }
  };

  // Split the hero title into arrays for letter-by-letter animation
  const heroTitle = "Smarter Systems,".split("");
  const secondLine = "Stronger Businesses".split("");

  // Styles
  const primaryButtonStyle: CSSProperties = {
    padding: '0.75rem 2rem',
    backgroundColor: 'var(--primary)',
    color: '#000',
    fontWeight: '500',
    border: 'none',
    display: 'inline-flex',
    alignItems: 'center',
    textDecoration: 'none',
    fontSize: '0.9rem',
    letterSpacing: '0.05em',
    textTransform: 'uppercase',
    transition: 'all 0.3s',
    fontFamily: 'var(--font-alt)'
  };

  const outlineButtonStyle: CSSProperties = {
    padding: '0.75rem 2rem',
    backgroundColor: 'transparent',
    color: 'var(--foreground)',
    border: '1px solid rgba(255, 255, 255, 0.2)',
    fontWeight: '500',
    display: 'inline-flex',
    alignItems: 'center',
    textDecoration: 'none',
    fontSize: '0.9rem',
    letterSpacing: '0.05em',
    textTransform: 'uppercase',
    transition: 'all 0.3s',
    fontFamily: 'var(--font-alt)'
  };

  const heroStyle: CSSProperties = {
    backgroundColor: '#000',
    minHeight: '100vh',
    padding: '7rem 1.5rem 5rem',
    position: 'relative',
    zIndex: 1,
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center'
  };

  const containerStyle: CSSProperties = {
    maxWidth: '1280px',
    width: '100%',
    margin: '0 auto'
  };

  const heroContentStyle: CSSProperties = {
    display: 'grid',
    gridTemplateColumns: 'repeat(1, 1fr)',
    gap: '3rem',
    alignItems: 'center'
  };

  const heroTextContainerStyle: CSSProperties = {
    gridColumn: '1 / -1'
  };

  const gradientTextStyle: CSSProperties = {
    backgroundImage: 'linear-gradient(45deg, var(--primary), #4b9fff)',
    backgroundSize: '100%',
    backgroundClip: 'text',
    WebkitBackgroundClip: 'text',
    WebkitTextFillColor: 'transparent',
    position: 'relative',
    display: 'inline-block'
  };

  const shimmerStyle: CSSProperties = {
    position: 'absolute',
    top: 0,
    left: 0,
    width: '100%',
    height: '100%',
    background: 'linear-gradient(90deg, transparent, rgba(255,255,255,0.2), transparent)',
    backgroundSize: '200% 100%',
    animation: 'shimmer 2s infinite',
    zIndex: 1,
    pointerEvents: 'none'
  };

  const headingOneStyle: CSSProperties = {
    fontSize: 'clamp(2.5rem, 8vw, 4.5rem)',
    lineHeight: 1.1,
    margin: '0 0 2rem 0',
    fontFamily: 'var(--font-display)',
    fontWeight: 700,
    textTransform: 'uppercase',
    letterSpacing: '0.02em',
    color: 'var(--foreground)'
  };

  const paragraphStyle: CSSProperties = {
    fontSize: 'clamp(1rem, 4vw, 1.25rem)',
    lineHeight: 1.6,
    marginBottom: '2.5rem',
    color: 'rgba(255, 255, 255, 0.8)',
    maxWidth: '600px',
    fontWeight: 300
  };

  const buttonGroupStyle: CSSProperties = {
    display: 'flex',
    gap: '1rem',
    flexWrap: 'wrap'
  };

  return (
    <section style={heroStyle}>
      <div style={containerStyle}>
        <div style={heroContentStyle}>
          <div style={heroTextContainerStyle}>
            <motion.h1 
              style={headingOneStyle}
              variants={headingVariants}
              initial="hidden"
              animate="visible"
            >
              {heroTitle.map((letter, index) => (
                <motion.span key={index} variants={letterVariants}>
                  {letter === " " ? "\u00A0" : letter}
                </motion.span>
              ))}
              <br />
              {secondLine.map((letter, index) => (
                <motion.span 
                  key={`second_${index}`} 
                  variants={letterVariants}
                  style={index === 0 ? gradientTextStyle : {}}
                >
                  {letter === " " ? "\u00A0" : letter}
                  {index === 0 && <div style={shimmerStyle}></div>}
                </motion.span>
              ))}
            </motion.h1>
            
            <motion.p
              style={paragraphStyle}
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.6, delay: 0.4 }}
            >
              Revolutionize your business with our cutting-edge solutions designed to 
              enhance operational efficiency and drive growth in today's digital landscape.
            </motion.p>
            
            <motion.div
              style={buttonGroupStyle}
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.6, delay: 0.6 }}
            >
              <motion.a
                href="#features"
                style={primaryButtonStyle}
                whileHover={{ 
                  scale: 1.05, 
                  boxShadow: '0 10px 25px rgba(var(--primary-rgb), 0.3)',
                  y: -5
                }}
                whileTap={{ scale: 0.95 }}
              >
                Explore Features
              </motion.a>
              
              <motion.a
                href="#contact"
                style={outlineButtonStyle}
                whileHover={{ 
                  scale: 1.05, 
                  backgroundColor: 'rgba(255, 255, 255, 0.05)',
                  borderColor: 'rgba(255, 255, 255, 0.3)',
                  y: -5
                }}
                whileTap={{ scale: 0.95 }}
              >
                Get Started
              </motion.a>
            </motion.div>
          </div>
        </div>
      </div>
      
      <style jsx global>{`
        @keyframes shimmer {
          0% {
            background-position: -200% 0;
          }
          100% {
            background-position: 200% 0;
          }
        }
      `}</style>
    </section>
  );
} 