"use client";

import React, { useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import { Check, Shield, Clock, Zap, Users, Layers } from 'lucide-react';
import { CSSProperties } from 'react';
import { featuresList } from '@/data/featuresList';

interface FeatureItemProps {
  icon: React.ReactNode;
  title: string;
  description: string;
}

const FeatureItem = ({ icon, title, description }: FeatureItemProps) => {
  const featureBoxStyle: CSSProperties = {
    padding: '2.5rem',
    backgroundColor: 'transparent',
    position: 'relative',
    height: '100%',
    borderLeft: '1px solid rgba(255, 255, 255, 0.1)',
    transition: 'all 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275)'
  };

  const iconContainerStyle: CSSProperties = {
    width: '3rem',
    height: '3rem',
    backgroundColor: 'transparent',
    border: '1px solid rgba(255, 255, 255, 0.2)',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    marginBottom: '1.5rem',
    transition: 'all 0.4s ease'
  };

  const titleStyle: CSSProperties = {
    fontSize: '1.125rem',
    fontWeight: '300',
    marginBottom: '1rem',
    color: 'var(--primary)',
    letterSpacing: '0.1em',
    textTransform: 'uppercase',
    fontFamily: 'var(--font-display)'
  };

  const descriptionStyle: CSSProperties = {
    fontSize: '0.95rem',
    color: 'var(--muted-100)',
    lineHeight: '1.7',
    fontWeight: '300',
    fontFamily: 'var(--font-alt)'
  };

  // Hover animation overlay
  const overlayStyle: CSSProperties = {
    position: 'absolute',
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    background: 'linear-gradient(135deg, rgba(255, 255, 255, 0.05) 0%, rgba(255, 255, 255, 0) 100%)',
    opacity: 0,
    transition: 'opacity 0.4s ease, transform 0.4s ease',
    transform: 'scale(0.95)',
    pointerEvents: 'none',
    zIndex: 0
  };

  return (
    <motion.div
      whileHover={{ 
        y: -10,
        borderLeft: '1px solid rgba(255, 255, 255, 0.3)',
        boxShadow: '0 20px 40px rgba(0, 0, 0, 0.3)',
        transform: 'perspective(1000px) rotateX(2deg) rotateY(2deg)'
      }}
      initial={{ opacity: 0, y: 20 }}
      whileInView={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.6 }}
      viewport={{ once: true, amount: 0.3 }}
      style={featureBoxStyle}
    >
      {/* Hover overlay */}
      <motion.div 
        style={overlayStyle} 
        whileHover={{ 
          opacity: 1,
          transform: 'scale(1)'
        }}
      />
      
      <div style={{ position: 'relative', zIndex: 1 }}>
        <motion.div 
          style={iconContainerStyle}
          whileHover={{
            borderColor: 'var(--primary)',
            transform: 'rotate(10deg)'
          }}
        >
          {icon}
        </motion.div>
        <h3 style={titleStyle}>{title}</h3>
        <p style={descriptionStyle}>{description}</p>
      </div>
    </motion.div>
  );
};

export default function Features() {
  const sectionStyle: CSSProperties = {
    backgroundColor: '#000',
    padding: '8rem 1.5rem',
    position: 'relative',
    borderTop: '1px solid rgba(255, 255, 255, 0.05)',
    borderBottom: '1px solid rgba(255, 255, 255, 0.05)'
  };

  const containerStyle: CSSProperties = {
    maxWidth: '1280px',
    margin: '0 auto'
  };

  const headerStyle: CSSProperties = {
    textAlign: 'center',
    marginBottom: '5rem'
  };

  const titleStyle: CSSProperties = {
    fontSize: 'clamp(2rem, 5vw, 3rem)',
    fontWeight: 700,
    marginBottom: '1.5rem',
    fontFamily: 'var(--font-display)',
    letterSpacing: '0.02em',
    textTransform: 'uppercase'
  };

  const subtitleStyle: CSSProperties = {
    fontSize: 'clamp(1rem, 3vw, 1.25rem)',
    lineHeight: 1.6,
    color: 'rgba(255, 255, 255, 0.7)',
    maxWidth: '700px',
    margin: '0 auto',
    fontWeight: 300
  };

  const featuresGridStyle: CSSProperties = {
    display: 'grid',
    gridTemplateColumns: 'repeat(auto-fill, minmax(300px, 1fr))',
    gap: '2rem'
  };

  const featureBoxStyle: CSSProperties = {
    padding: '2.5rem',
    backgroundColor: 'rgba(255, 255, 255, 0.02)',
    borderRadius: '4px',
    border: '1px solid rgba(255, 255, 255, 0.05)',
    height: '100%',
    position: 'relative',
    overflow: 'hidden',
    transition: 'all 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94)'
  };

  const iconContainerStyle: CSSProperties = {
    width: '3.5rem',
    height: '3.5rem',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    backgroundColor: 'rgba(255, 255, 255, 0.03)',
    borderRadius: '12px',
    marginBottom: '1.5rem',
    border: '1px solid rgba(255, 255, 255, 0.08)',
    color: 'var(--primary)',
    transition: 'all 0.5s'
  };

  const titleFeatureStyle: CSSProperties = {
    fontSize: '1.25rem',
    fontWeight: 600,
    marginBottom: '1rem',
    fontFamily: 'var(--font-display)',
    letterSpacing: '0.02em'
  };

  const descriptionStyle: CSSProperties = {
    fontSize: '0.95rem',
    lineHeight: 1.6,
    color: 'rgba(255, 255, 255, 0.7)'
  };

  const overlayStyle: CSSProperties = {
    position: 'absolute',
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    backgroundImage: 'linear-gradient(135deg, rgba(var(--primary-rgb), 0), rgba(var(--primary-rgb), 0.05), rgba(255, 255, 255, 0.05))',
    opacity: 0,
    transition: 'opacity 0.5s ease, transform 0.5s ease',
    pointerEvents: 'none',
    zIndex: 1
  };

  return (
    <section id="features" style={sectionStyle}>
      <div style={containerStyle}>
        <div style={headerStyle}>
          <motion.h2 
            style={titleStyle}
            initial={{ opacity: 0, y: 20 }}
            whileInView={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.6 }}
            viewport={{ once: true, amount: 0.3 }}
          >
            Our Advanced Features
          </motion.h2>
          <motion.p 
            style={subtitleStyle}
            initial={{ opacity: 0, y: 20 }}
            whileInView={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.6, delay: 0.2 }}
            viewport={{ once: true, amount: 0.3 }}
          >
            Discover how our cutting-edge solutions can transform your business with these powerful features designed for the modern enterprise.
          </motion.p>
        </div>
        
        <div style={featuresGridStyle}>
          {featuresList.map((feature, index) => (
            <motion.div
              key={index}
              initial={{ opacity: 0, y: 30 }}
              whileInView={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.5, delay: index * 0.1 }}
              viewport={{ once: true, amount: 0.3 }}
              whileHover={{
                y: -10,
                transition: { duration: 0.4 }
              }}
            >
              <motion.div 
                style={featureBoxStyle}
                whileHover={{
                  borderColor: 'var(--primary)',
                  boxShadow: '0 10px 30px rgba(var(--primary-rgb), 0.15)'
                }}
              >
                <motion.div
                  style={overlayStyle}
                  whileHover={{
                    opacity: 1,
                    transform: 'translateY(0) scale(1.05)'
                  }}
                  initial={{
                    transform: 'translateY(100%) scale(1)'
                  }}
                />
                
                <motion.div 
                  style={iconContainerStyle}
                  whileHover={{
                    backgroundColor: 'rgba(var(--primary-rgb), 0.1)',
                    rotate: 5
                  }}
                >
                  <feature.icon size={24} />
                </motion.div>
                
                <h3 style={titleFeatureStyle}>{feature.title}</h3>
                <p style={descriptionStyle}>{feature.description}</p>
              </motion.div>
            </motion.div>
          ))}
        </div>
      </div>
    </section>
  );
} 